body {
  font-family: "Poppins", sans-serif;
}
body p {
  font-size: 14px;
}

::selection {
  background: var(--primary);
  color: var(--white);
}

h2 {
  @apply font-extrabold text-5xl;
  color: var(--primary);
}

#hero {
  position: relative;
}
#hero .subtitle {
  letter-spacing: 14px;
  font-size: 18px;
}

.projects-newest {
  display: flex;
  justify-content: center;
  align-items: center;
}
.projects-newest__item {
  margin: 0 2rem;
  cursor: pointer;
}
.projects-newest__item img {
  width: 375px;
  transition: 0.4s;
}
.projects-newest__item img:hover {
  transform: translateY(-10px);
}

.dev-section {
  position: relative;
}
.dev-section .back-illustration {
  position: absolute;
  top: -75px;
  left: 0;
  z-index: -1;
}